@import '../../variables'

.contact
  opacity: 1
  width: 36.875%
  height: 78%
  background-color: $mainbg
  border-radius: 4px
  transition: margin-left .5s ease-in-out, opacity 1s ease-in-out
  margin-left: 26.9%
  overflow-y: scroll
  &-hidden
    opacity: 0
    width: 0
    margin-left: 0%
  &_head
    border-bottom: 1px solid $bordercolor
    padding: 3% 5%
    p
      font-size: 1.25rem
      font-weight: 600
      color: $whitetext
      position: relative
      span
        color: $maintext
      &:before
        content: ""
        background: linear-gradient(135deg,rgba(255, 152, 0, 0.4) 0%, rgba(255, 152, 0, 0.01) 100%)
        position: absolute
        left: -12px
        top: 3px
        width: 30px
        height: 30px
        border-radius: 100%
  &_address
    padding: 5%
    &-list
      list-style-type: none
      display: grid
      grid-template-columns: auto auto
      gap: 10%
      padding-top: 5%
      li
        color: $helptext
        font-weight: 500
        font-size: 0.875rem
        span
          color: $whitetext
          text-transform: uppercase

          &:after
            content: '. . . . . . . . .'
  &_form
    padding-bottom: 5%
    &-head
      border-bottom: 1px solid $bordercolor
      padding: 3% 5%
    p
      font-size: 1.25rem
      font-weight: 600
      color: $whitetext
      position: relative
      span
        color: $maintext
      &:before
        content: ""
        background: linear-gradient(135deg,rgba(255, 152, 0, 0.4) 0%, rgba(255, 152, 0, 0.01) 100%)
        position: absolute
        left: -12px
        top: 3px
        width: 30px
        height: 30px
        border-radius: 100%
    &-form
      padding: 3% 5%
      width: 100%
      display: flex
      flex-direction: column
      justify-content: center
      align-items: center
      form
        width: 100%

      .form-success
        visibility: hidden
        transition: .5s ease-in-out
        width: 80%
        text-align: center
        color: $whitetext

      .form-success-active
        visibility: visible
        width: 80%
        text-align: center
        border: 1px solid $maintext
        padding: 10px
        margin-top: 10px
        color: $whitetext
        font-weight: 500
        font-size: 1rem
        transition: .5s ease-in-out
      &-namemail
        width: 100%
        display: flex
        justify-content: space-between
        align-items: center
        &-name, &-mail
          width: 48%
          color: red
          font-weight: 300
          font-size: .875rem
          input
            width: 100%
            height: 50px
            background: none
            outline: none
            border: none
            border-bottom: 1px solid #666
            color: $whitetext
            font-size: 1rem
      textarea
        width: 100%
        height: 100px
        resize: none
        background: none
        outline: none
        border: none
        border-bottom: 1px solid #666
        padding-top: 3%
        color: $whitetext
        font-size: 1rem
      .textarea-error
        color: red
        font-weight: 300
        font-size: .875rem
      button
        background: none
        border: none
        outline: none
        color: $whitetext
        font-size: 1rem
        display: flex
        align-items: center
        padding-top: 3%
        text-transform: uppercase
        font-weight: 500
        cursor: pointer
        &:hover
          color: $maintext
        .form-btn
          font-size: 1.25rem
          padding-left: 2%