@import '../../variables'

.profile
  background-color: $mainbg
  height: 100%
  width: 100%
  position: relative
  border-radius: 4px
  &_bg
    height: 50%
    width: 100%
    position: absolute
    top: 0
    left: 0
    overflow: hidden
    background-image: url("../../assets/images/gotovo.png")
    background-repeat: no-repeat
    background-position: 50%
    background-size: cover
    &:after
      content: ""
      -webkit-transform: rotate(-8deg)
      background-color: #31313a
      position: absolute
      right: -25%
      bottom: -63%
      width: 100%
      height: 70%
    &:before
      content: ""
      position: absolute
      background-color: #31313a
      left: -25%
      bottom: -63%
      width: 100%
      height: 70%
      transform: rotate(8deg)
      -webkit-transform: rotate(8deg)
  &_pfp
    width: 200px
    height: 200px
    position: absolute
    top: 50%
    left: 50%
    transform: translate(-50%, -50%)
    border: 3px solid #31313a
    border-radius: 100%
    &:before
      content: ""
      background: linear-gradient(135deg,rgba(255, 152, 0, 0.4) 0%, rgba(255, 152, 0, 0.01) 100%)
      position: absolute
      top: 5px
      left: -5px
      width: 100%
      height: 100%
      border-radius: 100%
      z-index: -1
    img
      width: 100%
      height: 100%
      object-fit: contain
      border-radius: 100%
  &_info
    position: absolute
    top: 65%
    left: 50%
    transform: translateX(-50%)
    &-name
      color: $whitetext
      font-size: 2.125rem
      font-weight: 600
    &-typing
      padding-top: 5%
      color: $maintext
      font-size: 1rem
      font-weight: 600
      text-align: center
    &-social
      @include centering
      padding-top: 15%
      gap: 10%
      a
        font-size: 1.5rem
        color: $whitetext
        transition: color .4s ease-in-out
        &:hover
          color: $maintext
  &_btns
    position: absolute
    bottom: 0
    width: 100%
    height: 8%
    button
      width: 50%
      height: 100%
      background-color: transparent
      border: none
      border-top: 1px solid $bordercolor
      color: $whitetext
      font-weight: 700
      font-size: 1rem
      text-transform: uppercase
      cursor: pointer
      transition: color .4s ease-in-out
      &:hover
        color: $maintext
    .cv
      border-right: .5px solid #474751
    .contact
      border-left: .5px solid #474751
  &:before
    position: absolute
    content: ""
    background-color: $mainbg
    opacity: .3
    left: -15px
    top: -15px
    width: 100%
    height: 100%
    border-radius: 4px

