@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap')
@import "./variables"

*
  margin: 0
  padding: 0
  box-sizing: border-box
  font-family: 'Poppins', sans-serif
  //outline: 1px solid red

body
  height: 100vh

#root
  height: 100%

.container
  height: 100%
  background-color: #1c1c21
  @include centering
  position: relative
  overflow: hidden


/* width */
::-webkit-scrollbar
  width: 5px


/* Track */
::-webkit-scrollbar-track
  box-shadow: inset 0 0 5px $mainbg
  border-radius: 10px


/* Handle */
::-webkit-scrollbar-thumb
  background: #65656b
  border-radius: 10px


/* Handle on hover */
::-webkit-scrollbar-thumb:hover
  background: #65656b