@import '../../variables'

.about
  opacity: 1
  width: 36.875%
  height: 78%
  background-color: $mainbg
  border-radius: 4px
  transition: margin-left .5s ease-in-out, opacity 1s ease-in-out
  margin-left: 26.9%
  overflow-y: scroll
  &-hidden
    opacity: 0
    width: 0
    margin-left: 0
  &_me
    padding-bottom: 10%
    &-head
      border-bottom: 1px solid $bordercolor
      padding: 3% 5%
      p
        font-size: 1.25rem
        font-weight: 600
        color: $whitetext
        position: relative
        span
          color: $maintext
        &:before
          content: ""
          background: linear-gradient(135deg,rgba(255, 152, 0, 0.4) 0%, rgba(255, 152, 0, 0.01) 100%)
          position: absolute
          left: -12px
          top: 3px
          width: 30px
          height: 30px
          border-radius: 100%
    &-desc
      color: $helptext
      padding: 5%
      text-align: justify
    &-more
      padding: 0 5%
      ul
        list-style-type: none
        display: grid
        grid-template-columns: auto auto
        gap: 13%
        li
          color: $helptext
          font-weight: 500
          span
            color: $whitetext
            text-transform: uppercase
            font-size: 0.875rem
            &:after
              content: '. . . . . . . . .'
  &_services
    width: 100%
    padding-bottom: 10%
    &-head
      border-bottom: 1px solid $bordercolor
      padding: 3% 5%
      p
        font-size: 1.25rem
        font-weight: 600
        color: $whitetext
        position: relative
        span
          color: $maintext
        &:before
          content: ""
          background: linear-gradient(135deg,rgba(255, 152, 0, 0.4) 0%, rgba(255, 152, 0, 0.01) 100%)
          position: absolute
          left: -12px
          top: 3px
          width: 30px
          height: 30px
          border-radius: 100%
    &-list
      width: 100%
      display: flex
      flex-wrap: wrap
      justify-content: center
      &-card
        width: 50%
        border: 1px solid $bordercolor
        height: 247px
        padding: 5%
      .front, .design, .source
        border-top: none
      .front
        border-left: none
      .design
        border-right: none
        border-left: none

      h4
        color: $whitetext
        font-size: 1.0625rem
        font-weight: 500
        padding-top: 3%
      p
        color: $helptext
        line-height: 1.75rem
        padding-top: 3%
      .card-icon
        background: linear-gradient(135deg,rgba(255, 152, 0, 0.4) 0%, rgba(255, 152, 0, 0.01) 100%)
        width: 3.75rem
        height: 3.75rem
        border-radius: 100%
        color: $maintext
        display: grid
        place-items: center
        font-size: 2rem
  &_certificates
    &-head
      border-bottom: 1px solid $bordercolor
      padding: 3% 5%
      p
        font-size: 1.25rem
        font-weight: 600
        color: $whitetext
        position: relative
        span
          color: $maintext
        &:before
          content: ""
          background: linear-gradient(135deg,rgba(255, 152, 0, 0.4) 0%, rgba(255, 152, 0, 0.01) 100%)
          position: absolute
          left: -12px
          top: 3px
          width: 30px
          height: 30px
          border-radius: 100%
    &-first
      padding: 5%
      p
        color: $whitetext
        span
          color: $maintext
      &-cisco
        width: 300px
        height: 300px
        img
          width: 100%
          height: 100%
          object-fit: contain
      button
        width: 150px
        height: 38px
        background: none
        border: 1px solid $whitetext
        outline: none
        color: $maintext
        font-size: 1rem
        font-weight: 500
        cursor: pointer
        &:active
          transform: scale(.99)
  &_clients
    &-head
      border-bottom: 1px solid $bordercolor
      padding: 3% 5%
      p
        font-size: 1.25rem
        font-weight: 600
        color: $whitetext
        position: relative
        span
          color: $maintext
        &:before
          content: ""
          background: linear-gradient(135deg,rgba(255, 152, 0, 0.4) 0%, rgba(255, 152, 0, 0.01) 100%)
          position: absolute
          left: -12px
          top: 3px
          width: 30px
          height: 30px
          border-radius: 100%
    &-list
      display: flex
      &-card
        width: 25%
        display: flex
        justify-content: center
        align-items: center
        height: 160px
        border-right: 1px solid $bordercolor
        text-align: center
        color: $whitetext
        .client-logo
          width: 50%
          img
            object-fit: contain
            width: 100%
            height: 100%



